.button {
  padding: 5px 10px;
  border: 1px solid $grey-dark;
  background-color: $grey;
  cursor: pointer;
  width: 115px;
  font-size: 12px;
  line-height: 16px;
  color: $black;

  @include robotocondensed;
}

.button:hover {
  background-color: $silver;
}

.button:active {
  background-color: $silver;
  box-shadow: 0 0 2px 2px $grey-dark;
}
