.page-header {
  margin: 0 0 30px 0;
}

.page-header__wrapper input {
  padding: 5px 10px;
  border: 1px solid $grey-dark;
  background-color: $white;
}

.page-header__ip-block {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  margin: 0 0 10px 0;

  @media (max-width: 767px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.page-header__ip-address {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  @media (max-width: 767px) {
    margin: 0 0 10px 0;
  }
}

.page-header__ip-address label:first-of-type {
  @media (max-width: 767px) {
    width: 100%;
    margin: 0 0 7px 0;
  }
}

.page-header__ip-address input:first-of-type {
  margin: 0 0 0 34px;

  @media (max-width: 1023px) {
    margin: 0 0 0 40px;
  }

  @media (max-width: 767px) {
    margin: 0;
  }
}

.page-header__ip-address input:last-of-type {
  width: 85px;
}

.page-header__ip-address span:first-of-type {
  margin: 0 15px 0;

  @media (max-width: 767px) {
    margin: 0 5px 0;
  }
}

.page-header__ip-buttons {
  @media (max-width: 767px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
}

.page-header__ip-block button:first-of-type {
  margin: 0 0 0 30px;

  @media (max-width: 767px) {
    margin: 0;
  }
}

.page-header__ip-block button:last-of-type {
  margin: 0 0 0 15px;

  @media (max-width: 767px) {
    margin: 0 0 0 9px;
  }
}

.page-header__log-block {
  display: grid;
  grid-template-columns: min-content 1fr;
}

.page-header__log-block input {
  margin: 0;
  width: 100%;
}

.page-header__log-block button {
  margin: 0 10px 0 572px;

  @media (max-width: 1023px) {
    margin: 0 8px 0 0;
  }
}
