.result__wrapper {
  display: flex;
  flex-direction: column;
}

.result__tabs ul {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0;
  margin: 0;
  border-bottom: 1px solid $grey-dark;
}

.result__tabs li {
  margin: 0;
  border: 1px solid $grey-dark;
  border-bottom: none;
  padding: 5px 5px 0 5px;
  cursor: pointer;
}

.result__tabs li:first-of-type {
  background-color: $white;
  border-bottom: 1px solid $white;
  padding: 5px;
}

.result__tabs li:hover {
  background-color: $grey;
}

.result__data {
  border-top: 1px solid $white;
}

