.miner-data {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: 1228px) {
    flex-direction: column;
  }

  @media (max-width: 1023px) {
    flex-direction: column;
  }
}

.miner-data__info {
  width: 28%;

  @media (max-width: 1228px) {
    width: 100%;
    display: flex;
    padding: 15px 0 0 0;
    margin: 0 0 10px 0;
  }

  @media (max-width: 1023px) {
    display: flex;
    flex-wrap: wrap;
  }

  @media (max-width: 767px) {
    flex-direction: column;
    padding: 0;
  }
}

.miner-data__list ul {
  margin: 0;
  padding: 0;
}

.miner-data p {
  display: block;
  margin: 0;
  padding: 0;
}

.miner-data__list {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
  grid-column-gap: 5px;
  padding: 15px 0 5px 10px;
  border: 1px solid $grey-dark;
  border-radius: 5px;
  margin: 0 0 15px 0;

  @media (max-width: 1228px) {
    padding: 20px 5px 0 0;
    border: none;
  }

  @media (max-width: 1023px) {
    width: 32%;
    padding: 15px 10px 0;
  }

  @media (max-width: 767px) {
    width: 100%;
  }

  &--description {
    border: none;
    padding: 15px 0 0 10px;
  }

  &--other {
    border: none;
    padding: 0 10px;

    @media (max-width: 1023px) {
      order: 4;
      margin: 0 10px 0 0;
    }

    @media (max-width: 767px) {
      order: 5;
      margin: 0;
    }
  }

  &--psu {
    @media (max-width: 1023px) {
      order: 5;
    }

    @media (max-width: 767px) {
      order: 4;
    }
  }
}

.miner-data__list:not(:last-child) {
  @media (max-width: 1228px) {
    border-right: 1px solid $grey-dark;
    border-radius: 0;
    margin: 0 10px 0 0;
  }

  @media (max-width: 1023px) {
    border-right: none;
    margin: 0 10px 12px 0;
  }

  @media (max-width: 767px) {
    margin: 0 0 17px 0;
  }
}

.miner-data__list:nth-child(n+2):nth-child(-n+4) {
  @media (max-width: 1023px) {
    border: 1px solid $grey-dark;
    border-radius: 5px;
  }
}

.miner-data__list b {
  grid-row: 1 / 2;
  grid-column: 1 / -1;
  margin: 0 0 5px 0;
  position: absolute;
  top: -24px;
  left: 0;
  background-color: $silver;
  padding: 0 5px;

  @media (max-width: 1228px) {
    padding: 0;
  }
}

.miner-data__list li {
  margin: 0 0 5px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content;
}

.miner-data__list li div {
  display: flex;
  align-items: center;
}

.miner-data__list li div span {
  display: block;
  margin: 0 3px 0 0;
  padding: 2px 5px;
}

// Блок с чипами

.miner-data__chips {
  margin: 0 0 0 auto;
  width: 70%;

  @media (max-width: 1228px) {
    margin: 0;
    width: 100%;
  }
}

.miner-data__board-list {
  list-style: none;
  display: flex;
  flex-wrap: nowrap;
  counter-reset: item;
  padding: 35px 0 0 0;
  margin: 0;
  width: 100%;

  @media (max-width: 1228px) {
    width: 100%;
    justify-content: center;
  }

  @media (max-width: 670px) {
    flex-direction: column;
    padding: 0;
  }
}

.miner-data__board-item {
  position: relative;
  width: 100%;

  @media (max-width: 670px) {
    padding: 35px 0 0 0;
    margin: 0 0 10px 0;
  }
}

.miner-data__board-item::before {
  counter-increment: item;
  content: counter(item);
  position: absolute;
  font-size: 20px;
  line-height: 26px;
  color: $blue;
  top: -31px;
  left: 14px;

  @include robotocondensed-bold;

  @media (max-width: 670px) {
    top: 0;
    left: 14px;
  }
}

.miner-data__board-item:not(:first-child) {
  margin: 0 0 0 15px;

  @media (max-width: 1228px) {
    margin: 0 0 0 40px;
  }

  @media (max-width: 1023px) {
    margin: 0 0 0 20px;
  }

  @media (max-width: 767px) {
    margin: 0 0 0 15px;
  }

  @media (max-width: 670px) {
    margin: 0 0 10px 0;
  }
}

.miner-data__board-item select {
  margin: 0 0 5px 0;
}

.miner-data__board-item ul {
  padding: 0;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
}

.miner-data__board-item ul li {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: min-content min-content min-content;
  border: 1px solid $grey-dark;
}

.miner-data__board-item ul li span:first-of-type {
  grid-column: 1 / 2;
  grid-row: 1 / 2;

  @include robotocondensed;
  font-size: 10px;
  line-height: 12px;
}

.miner-data__board-item ul li span:first-of-type+span {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  justify-self: end;
  @include robotocondensed;
  font-size: 10px;
  line-height: 12px;
}

.miner-data__board-item ul li span:nth-child(3n) {
  grid-column: 1 / -1;
  grid-row: 2 / 3;
  justify-self: center;
}

.miner-data__board-item ul li span:last-of-type {
  grid-column: 1 / -1;
  grid-row: 3 / 4;
  justify-self: center;
  @include robotocondensed;
  font-size: 12px;
  line-height: 14px;
  font-size: 10px;
  line-height: 12px;
}
