.temp {

  &--cold-extra {
    background-color: $cold-extra;
  }

  &--cold {
    background-color: $cold;
  }

  &--cool {
    background-color: $cool;
  }

  &--normal-cool {
    background-color: $normal-cool;
  }

  &--normal {
    background-color: $normal;
  }

  &--normal-warm {
    background-color: $normal-warm;
  }

  &--warm {
    background-color: $warm;
  }

  &--warm-extra {
    background-color: $warm-extra;
  }

  &--hot {
    background-color: $hot;
  }
}
