.page {
  height: 100%;
  font-size: 12px;
  line-height: 16px;
  color: $black;

  @include robotocondensed;
}

.page__body {
  margin: 0 auto;
  padding: 15px 10px;
  max-width: 1468px;
  background-color: $silver;
}

.page span {
  @include robotocondensed-bold;
}

.page select {
  padding: 2px 4px;
  border: 1px solid $grey-dark;
  background-color: $grey;
  cursor: pointer;
  width: 100%;

  @include robotocondensed-bold;
}
